import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes = [ 
  {
    path: '/Home',
    name: 'Home',
    component: ()=>import("../components/Home.vue"),
    children: [
      {
        path: "Notprocessed",
        name: "Notprocessed",
        meta: {
          title: "首页"
        },
        component: ()=> import("../views/Home/Notprocessed.vue"),
      },
      {
        path: "Send",
        name: "Send",
        meta: {
          title: "首页"
        },
        component: ()=>import("../views/Home/Send.vue"),
      },
      {
        path: "Processed",
        name: "Processed",
        meta: {
          title: "首页"
        },
        component: ()=>import("../views/Home/Processed.vue"),
      }
    ]
  },
  {
    path: "/Login",
    component: () => import("../views/login/Login.vue"),
    meta: {
      title: "登录"
    }
  },
  {
    path: "/",
    redirect: "/Home/Notprocessed"
  },
  {
    path: "/overtimeEdit/:id",
    props: true,
    component: () => import("../views/overtimeEdit.vue")
  },
  {
    path: "/vacationEdit/:id",
    props: true,
    component: () => import("../views/vacationEdit.vue")
  }
]

const router = new Router({ 
  // mode: "history",
  routes 
});

router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  next()
})

export default router;


